@import "vars";

.content {
    a:hover {
        text-decoration: underline;
    }
    ol {
        counter-reset: list;
    }
    ol > li {
        list-style-position: none;
        position: relative;
        list-style: none;
        margin-top: 5px;
        margin-bottom: 5px;
    }
    ul > li {
        margin: 5px 0 5px 0;
    }
    ul {
        padding: 0 20px 0 20px;;
    }
    ol {
        padding: 0 30px 0 30px;
    }
    ol > li:before {
        content: "[" counter(list, decimal) "] ";
        counter-increment: list;
        position: absolute;
        left: -30px;
    }
    blockquote {
        //border: 1px solid black;
        border-left: 5px solid #333333;
        margin: 20px 0 20px 0;
        padding: 2px 8px 2px 8px;
        font-style: italic;
    }
    .profile-picture {
        height: 2.1*$image-size;
        width: 1.4*$image-size;
        position: relative;
        margin: 10px 25px 10px 25px;
        float: left;
        border-radius: 0%;
        p & {
            padding: 0;
            margin: 0;
        }
        max-width: $profile-max-width
        // @media (max-width: $profile-max-width) {
        //     img {
        //         margin-top: -5px;
                
        //     }
            
        // }
    }
    @media all and (max-width: $med-width){
        .profile-picture{
            // margin-left: 30vw;
            height: 1.5*$image-size;
            width: 1.2*$image-size;
            display:inline-block;
            margin-top: 35px;
        }
    }
    img {
        text-align: center;
        width: 100%;
    }
}
